.body{
  font-family: "Proxima Nova", Helvetica, Arial; 
  margin: 0;
  padding: 0;
  height: 100vh;
}

#particles{
  height: 100%;
  background:rgb(243, 242, 242);
}

h2, h3 {
  font-family: "Proxima Nova", Helvetica, Arial;
}

.btnlink{
 background-color: #1d5797;
 font-family: "Proxima Nova", Helvetica, Arial;
}

.btnlink:hover{
  background-color: #ebf8ff;
  color: #1d5797;
  transition-duration: 0.2s;
}

.jumbotron {
  position: relative;
  margin:0px;
  border-radius: 0px;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  background-color: #5b6467;
  background-image: linear-gradient(180deg, #bebebe 0%, #868686 74%);
}

.navbar-dark{
  background: #1d5797 ;
  color: #ebf8ff;
  position: sticky;
  top:0;
  z-index: 1000;
  box-shadow: 0px 1px #979797;
  font-family: 'DubaiW23-Light',sans-serif;
}

.footer{
  background-color: #1d5797;
  bottom: 0;
  color: #ebf8ff;
  margin:50px 0px auto;
  padding: 20px 0px 20px 0px;
}

.nav-item{
  margin-left: 20px;
  font-size: 15px;
}


.navbar-nav{
  position:absolute;
  right : 10px;
}

.carousel-caption h3{
  padding-bottom: 350px;
  font-size: 51px;
  color:#ebf8ff;
  font-weight:900;
  font-family: "Proxima Nova", Helvetica, Arial;
}

.thumb{
  display: none !important;
}

.carousel-status{
  display: none !important;
}

.legend{
  background: transparent !important;
  padding-bottom: 350px !important;
  font-size: 60px !important;
  color:#ebf8ff !important;
  font-weight:900 !important;
}

.aboutus{
  height: 100vh;
}

.aboutusrow{
  margin-top: 100px;
}

.mission{
  height: 100vh;
}

.missionrow{
  margin-top: 100px;
}

.card{
  border-radius: 0%;
  border-color:#f2f2f2;
  box-shadow: 2px 3px #f2f2f2;
}

.card:hover{
  transform: scale(1.03);
  transition-duration: 0.5s;
}

.card-text{
  font-size: 14px;
  font-family:'"DubaiW23-Bold",sans-serif';
  color: #2a2c2c;
}

.card-title{
  font-family:'"DubaiW23-Bold",sans-serif';
  color: #2a2c2c;
}

img:not(#logo){
  box-shadow: 2px 3px #f2f2f2;
}

.qualitycontrol{
  height: 100vh;
}

.qualitycontrolrow{
  margin-top: 100px;
}

.services{
  height: 100vh;
}

.servicesrow{
  margin-top: 25px;
}

.valueadded{
  height: 100vh;
}

.valueaddedrow{
  margin-top: 50px;
}

.map-container{
  overflow:hidden;
  padding-bottom:56.25%;
  position:relative;
  height:0;
  }
  .map-container iframe{
  left:0;
  top:0;
  height:100%;
  width:100%;
  position:absolute;
  }

  .contactback{
    /* background-image: url("back.jpg"); */
    background-repeat: repeat;
    color: white;
    padding: 45px;
    opacity: 90%;
    border-radius: 5px;
  }

  .director{
    margin-top: 2em;
  }
